<template>
  <div>
    <el-form-item label="" v-if="item.hasOwnProperty('monday')">
      <el-radio-group v-model="form.monday" size="medium">
        <el-radio border :label="1"></el-radio>
        <el-radio border :label="0"></el-radio>
      </el-radio-group>
    </el-form-item>
    <!--  -->
    <template v-if="item.id === 24">
      <el-form-item label="">
        <el-input
          v-model="form.name"
          width="200"
          placeholder="5"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 15">
      <el-form-item label="">
        <strong></strong>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.day"
          type="week"
          format="yyyyWW"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 13">
      <el-form-item label="">
        <el-date-picker
          v-model="form.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.name"
          width="200"
          placeholder="5"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.msg"
          width="200"
          placeholder="12"
        ></el-input>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 11">
      <el-form-item
        :label="`${index + 1}`"
        v-for="(item, index) in form.tasks"
        :key="`task-${index}`"
      >
        <el-input
          v-model="item.name"
          placeholder="5"
          style="width: 200px; margin-right: 20px"
        ></el-input>
        <el-date-picker
          v-model="item.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <el-button
          icon="el-icon-plus"
          circle
          size="small"
          v-show="index === 0 && form.tasks.length < 4"
          @click="onAddItem(form.tasks.length + 1)"
        ></el-button>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id >= 1001 && item.id < 1002">
      <el-form-item label="">
        <el-input
          v-model="form.content"
          width="200"
          placeholder=""
        ></el-input>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 27 || item.id === 28">
      <el-form-item label="">
        <el-input
          v-model="form.name"
          width="200"
          placeholder="5"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder=""
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 29">
      <el-form-item label="">
        <el-color-picker v-model="form.highlight"></el-color-picker>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="form.date_range"
          type="daterange"
          align="right"
          unlink-panels
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          :picker-options="pickerOptions2"
        >
        </el-date-picker>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 30">
      <el-form-item label="">
        <el-select v-model="form.lan" placeholder="">
          <el-option key="cn" label="" value="cn"></el-option>
          <el-option key="en" label="" value="en"> </el-option>
        </el-select>
      </el-form-item>
    </template>
    <!--  -->
    <template v-if="item.id === 1004">
      <el-form-item label="">
        <el-input
          v-model="form.btn_name"
          width="50"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.link"
          width="50"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.size">
          <el-radio label="normal"></el-radio>
          <el-radio label="large"></el-radio>
        </el-radio-group>
      </el-form-item>
    </template>
  </div>
</template>
<script>
export default {
  props: ["item"],
  data() {
    return {
      form: {
        monday: 1,
        tasks: [{ day: "2022/12/31", name: "2022" }],
        name: "",
        day: "",
        content: "  Here is the custom text",
        highlight: "#77ac98",
        date_range: "",
        lan: "cn",
        btn_name: "Github",
        size: "normal",
        link: "",
      },
    };
  },
  computed: {
    pickerOptions: function () {
      return {
        firstDayOfWeek: this.form.monday ? 1 : 7,
      };
    },
    pickerOptions2: function () {
      return {
        disabledDate(time) {
          return time.getTime() < Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      };
    },
  },
  mounted() {
    console.log(this.item);
    let cur_date = new Date();
    this.form.created = `${cur_date.getFullYear()}/${
      cur_date.getMonth() + 1
    }/${cur_date.getDate()}`;
  },
  methods: {
    onAddItem(index) {
      this.form.tasks.push({
        name: `${index}`,
        day: "2022/12/31",
      });
    },
  },
  watch: {
    form: {
      handler(newV) {
        this.$emit("on-form", newV);
      },
      deep: true,
    },
  },
};
</script>
